* {
    box-sizing: border-box;
}
.payment-records-container{
background-color: #F2F5FA;
font-family: 'Inter', sans-serif;
height: auto;

max-width: 100%;         
overflow-x: hidden; 
    
}

.Payment-title, .due-title{
  margin: 10;
  font-size: 24px;
  font-weight: 600;
}

.record-search {
    padding: 10px 15px;
    font-size: 16px;
    border-radius: 8px;
    border: 1px solid #ccc;
    width: 250px;
}

.payment-records-container > .payment-record-header{
    
    color: #222;
    font-size: 30px;
    margin-bottom: 20px;
    padding: 10px 5px 0;
   
}

.payment-record-header {
      display: flex;
  justify-content: space-between; 
  align-items: center;
  padding: 10px;
  position: relative;
}

.policy-data-field {
    padding: 20px 5px 10px 5px;
    background-color: white;;
}
.client-payment-table {
    width: 100%;
    margin: 0 auto;
    padding: 5px 0 0 0;
    border-radius: 10px;
}

.client-payment-table > table {
    width: 100%;
    padding: 20px 5px 10px 5px;
    
}



.client-payment-table > table > thead > tr > th {
    padding: 10px;
    text-align: left;
    font-size: 20px;
    background-color: #F2F5FA;
  
   
}

.client-payment-table > table > tbody > tr > td {
    background-color: white;
    text-align: left;
    padding: 20px 5px 10px 5px;
  
    
}   

.payment-records-content > div.policy-data-field{  
    background-color: white;
    margin-bottom: 10px;
    border-radius: 8px;
    padding: 20px 5px 10px 5px;
     box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}

